import db, {auth} from '../firebase'
import {getCategories} from "../redux/reducers/categories-reducer";

export const api = {
    // getCategories() {
    //     return db.collection("Categories").get();
    // },

    createUserWithEmail(email, password, firstName, lastName){
        return auth.createUserWithEmailAndPassword(email, password)
            .then(user => {
                return db.collection("Users").doc(email).set({
                    firstName,
                    lastName,
                    email,
                    password
                })
                    .then(user => ({code: 200, user: {firstName, lastName, email}}))
                    .catch(error => ({code: 400, message: error.message}));
            })
            .catch(error => ({code: 400, message: error.message}));
    },

    getInitialData(){
        return Promise.all(
            [db.collection("Categories").get(),
                    db.collection("Reviews").get(),
                    db.collection("Courses").orderBy("rate", "desc").limit(6).get()]
        ).then(results => {
            const data = {
                categories: [],
                reviews: [],
                popularCourses: [],
            };
            results[0].forEach(doc => data.categories.push(doc.data()));
            results[1].forEach(doc => data.reviews.push(doc.data()));
            results[2].forEach(doc => data.popularCourses.push(doc.data()));
            return data;
        })
            .then(data => {
                Promise.all(data.popularCourses.map(course => course.category.get()))
                    .then(categories => categories.forEach((category, index) => data.popularCourses[index].category = category.data()));
                return data;
            })
            .catch(error => console.log(error));
    }
};