import React from 'react';
import styles from './LoginLayout.module.css';
import logo from '../../assets/img/logo.png';
import LoginWithButton from "../LoginWithButton/LoginWithButton";
import LoginForm from "../LoginForm/LoginForm";
import {NavLink} from "react-router-dom";

const LoginLayout = () => {
    return <div className={styles.wrapper}>
        <div className={styles.login}>
            <header>
                <img src={logo} alt={'Logo'}/>
            </header>
            <div className={styles.form}>
                <div className={styles.loginWithButtons}>
                    <LoginWithButton type={'google'} text={"Login With Google"}/>
                    <LoginWithButton type={'facebook'} text={"Login With Facebook"}/>
                </div>
                <span className={styles.label}>Or</span>
                <LoginForm/>
            </div>
            <footer>
                <span>Now to Udema?</span>
                <NavLink to={"/register"}><a href={"#"}>Sign up</a></NavLink>
            </footer>
        </div>
    </div>
};

export default LoginLayout;